.message {
    display: grid;
    //width: 0;
    height: 0;
    margin-left: 200px;
    border-style: solid;
    border-width: 100px 0 0 100px;
    border-color: transparent transparent transparent #cc0000;
    width: 1200px;
}

.imageAndText {
    position: relative;
    width: 527px;
    height: 60px;
    left: 330px;
    top: 0px;
    margin: 0 auto;
}

.imageAndText img {
    width: 50px;
    position: relative;
    margin-left: 477px;
    top: 60px;
}

.text {
    width: 465px;
    height: 60px;
    left: 635px;
    gap: calc(20px + 10%);
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    background: #0066cc;
    border-radius: 10px;

}

